/* eslint-disable no-param-reassign */
import { createSlice } from '@reduxjs/toolkit';
import entityApi from '../api/entityApi';

const initialState = {
    cars: [{
        updatedAt: null,
        createdAt: null,
        priceMax: null,
        priceMin: null,
        name: '',
        thumbnail: {
            path: null,
            mimetype: '',
            originalname: '',
            size: null,
        },
        description: '',
        categoryId: {
            name: '',
            description: '',
            id: '',
        },
        number: '',
        tank: null,
        colors: [],
        id: '',
    }, ],
    pageNumber: 0,
    pageLimit: {
        label: ' 3  ',
        value: 3,
    },
    count: 0,
    sortOption: {
        label: ' ',
        value: '',
    },
    categoryOption: {
        label: ' ',
        value: '',
    },
    isFetching: false,
};

export const carSlice = createSlice({
    name: 'car',
    initialState,
    reducers: {
        setCar: (state, action) => {
            state.cars = action.payload.data;
            state.count = action.payload.count;
        },
        setIsFetching: (state, action) => {
            state.isFetching = action.payload;
        },
        setPageNumber: (state, action) => {
            state.pageNumber = action.payload;
        },
        setPageLimit: (state, action) => {
            state.pageLimit = action.payload;
        },
        setSortOption: (state, action) => {
            state.sortOption = action.payload;
        },
        setategoryOption: (state, action) => {
            state.categoryOption = action.payload;
            state.pageNumber = 0;
        },
        resetFilters: (state) => {
            state.pageNumber = 0;
            state.pageLimit = initialState.pageLimit;
            state.sortOption = initialState.sortOption;
            state.categoryOption = initialState.categoryOption;
        },
    },
});

export const {
    setCar,
    setIsFetching,
    setPageNumber,
    setPageLimit,
    setSortOption,
    setategoryOption,
    resetFilters,
} = carSlice.actions;

export const getCar =
    ({ page, limit, options }) =>
    async(dispatch) => {
        dispatch(setIsFetching(true));
        try {
            const response = await entityApi.getEntity({
                entity: 'car',
                page,
                limit,
                options,
            });
            dispatch(setCar(response.data));
        } catch {
            console.log('getOrder slice error');
        }
        dispatch(setIsFetching(false));
    };

export default carSlice.reducer;