import { useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { Link, useNavigate } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import Input from '../../components/Input/Input';
import css from './RegistrationPage.module.scss';
import { registration, setAuthError } from '../../store/authSlice';
import AuthContainerBlock from '../../components/AuthContainerBlock/AuthContainerBlock';

function RegistrationPage() {
    const {
        register,
        handleSubmit,
        setError,
        formState: { errors },
    } = useForm({ mode: 'onBlur' });

    const dispatch = useDispatch();
    const navigate = useNavigate();

    const { isAuth, authError } = useSelector((state) => state.auth);

    useEffect(() => {
        if (isAuth) navigate('/admin/order-list/');
    }, [isAuth]);

    useEffect(() => {
        if (authError) {
            setError(
                '',
                { type: 'login', message: authError },
                { shouldFocus: false },
            );
            setError(
                ' ',
                { type: 'login', message: authError },
                { shouldFocus: false },
            );
        }
    }, [authError]);

    const onSubmit = (data) => {
        if (data[''] !== data[' ']) {
            setError(
                '',
                { type: 'login', message: '  ' },
                { shouldFocus: false },
            );
            setError(
                ' ',
                { type: 'login', message: '  ' },
                { shouldFocus: false },
            );
        } else {
            dispatch(setAuthError(''));
            dispatch(
                registration({
                    username: data[''],
                    password: data[''],
                }),
            );
        }
    };

    return (
        <AuthContainerBlock>
            <form onSubmit={handleSubmit(onSubmit)}>
                <h3 className={css.card_header}></h3>
                <Input
                    label=""
                    type="text"
                    placeholder="    "
                    register={register}
                    required
                    setError={setError}
                    minLength={{
                        value: 3,
                        message: '  3- ',
                    }}
                    errors={errors}
                />
                <Input
                    label=""
                    type="password"
                    placeholder=" "
                    register={register}
                    required
                    setError={setError}
                    minLength={{
                        value: 3,
                        message: '  3- ',
                    }}
                    errors={errors}
                />
                <Input
                    label=" "
                    type="password"
                    placeholder=" "
                    register={register}
                    required
                    setError={setError}
                    minLength={{
                        value: 3,
                        message: '  3- ',
                    }}
                    errors={errors}
                />
                <div className={css.card_footer}>
                    <Link
                        className={css.link}
                        to='/'
                        onClick={() => dispatch(setAuthError(''))}
                    >
                         
                    </Link>
                    <button className={css.standart_button} type="submit">
                        
                    </button>
                </div>
            </form>
        </AuthContainerBlock>
    );
}

export default RegistrationPage;
